<template>
  <div class="about">
    <h1>Профил корсника {{name}}</h1><br/><br/><br/>
    Напишите нови тњит:<br/>
    <input type="textarea" id="tnjit" max="200" size="80" v-model="sadrzaj"/><br/>
    <input type="button" value="Објави" v-on:click="dodaj()" /><br/><br/><br/>
    Повест тњитова<br/>
    <div class="spisak">
    <div class="ploce" v-for="p in podaci"><h5>{{p.korisnickoime}}</h5> <br/>
      &nbsp&nbsp&nbsp&nbsp {{p.sadrzaj}}
      <br/><br/>
      {{p.datum.slice(p.datum.indexOf('T')+1,p.datum.indexOf('.'))}} {{p.datum.slice(0,p.datum.indexOf('T'))}}
      </div>
  </div>
  </div>
</template>

<script>
export default {

  props: {
    msg: String,


  },
  data(){
  return{
    name:"",
    prijavljen:false,
    podaci:[],
    sadrzaj:''
    }
  },

  async created(){
  let id=localStorage.getItem('id');
  
  if(id){
    await fetch('https://vue-rest.herokuapp.com/get?id='+id)
      .then(obj=>obj.json() )
        .then(res=>{
            this.podaci=res[0]
            this.name=this.podaci[0].korisnickoime;
          })
        }
  },
  methods:{
    dodaj(){
      let id=localStorage.getItem('id');
      let token=localStorage.getItem('token');
      fetch('https://vue-rest.herokuapp.com/get', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' ,'Access-Control-Allow-Origin': '*'},
        credentials: 'include',
        body: JSON.stringify({Id:id,Sadrzaj:this.sadrzaj,Token:token})
      }).then(data=>data.json())
      .then(resp=>{ 
        if(resp.status!=400 || resp.status!=500){
          this.$socket.emit('comment',{sadrzaj:resp.sadrzaj,korisnickoime:this.name,datum:resp.datum,doleglasovi:0,goreglasovi:0});
          this.$router.go();
        }
        else{
          alert("Greska u unosa podataka ili upita");}
        });
    }
  }
}
</script>